<template>
  <div class="padding20">
    <orderFormList></orderFormList>
  </div>
</template>

<script type="text/ecmascript-6">
  import orderFormList from '../orderFormList/orderFormList'

  export default {
  	data(){
  		return {
  			orderList:[]
  		}
  	},
    components:{
      orderFormList
    },
    created(){

    	loginStatus(this);

    	this.downloadOrderList();

    },
    methods:{
    	downloadOrderList:function(){

    		var url = path + "/index/companyorder/index?user_id="+this.userId+"&status=-10";

    		console.log(url)

		    this.$http.get(url).then(function(r){
		        var td = r.data;
		        this.orderList = td;
		    })

    	}
    }
  }
</script>

<style media="screen">
  
</style>



